<template>
    <div>
        <h2>글보기페이지</h2>
        번호 : {{article.num}}<br>
        글제목 : {{article.title}}<br>
        <hr>
        {{article.content}}
        <hr>
        <button @click="edit">수정</button> <button @click="del">삭제</button><br>
        <router-link to="/">메인페이지로</router-link>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            article: {}
        }
    },
    created() {
        console.log(this.$route.params.num)
        axios.get("http://3.38.178.54:9999/board/getArticle/"+this.$route.params.num).then(({data}) => this.article = data)
    },
    methods: {
        edit() {
            this.$router.push("/edit/" + this.article.num);
        },
        async del() {
            await axios.get("http://3.38.178.54:9999/board/del/"+this.$route.params.num)
            this.$router.push("/")
        }
    },
};
</script>

